<template>
  <div id="stpage">
    <el-steps style="position:absolute;top:7%;left:5%;width:90%;text-align:left" :active="active">
      <el-step title="步骤 1" description="请点击上传按钮，选择并上传一张图片。">
        <i slot="icon" class="el-icon-upload"> </i>
      </el-step>
      <el-step title="步骤 2" description="请点击上传按钮，选择并上传一张图片。">
        <i slot="icon" class="el-icon-upload"> </i>
      </el-step>
      <el-step title="步骤 3" icon="" description="请点击下一步，让系统开始合成图片。">
        <i slot="icon" class="el-icon-picture"> </i>
      </el-step>
    </el-steps>
    <el-button style="position:absolute;left:5%; bottom:10%;" @click="next"> 
      <template v-if="active!=3">下一步</template> 
      <template v-else> 重新开始 </template>
    </el-button>
  </div>
</template>

<script>
export default {
    data() {
      return {
        active: 0
      }
    },
    methods: {
      next() {
        if (this.active++ > 2) this.active = 0
        console.log(this.active);
      }
    }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
@import "../index";
#stage{
  position:absolute;
  width:100%;
  height:100%;
}

</style>



